body{padding:0;margin:0;font-family:sans-serif;font-size:1rem;font-weight:400;line-height:1.5;color:#000;background-color:#e9ecef}#layout-error{padding:2.5rem .75rem;margin:3.125rem .5rem 0;text-align:center;background-color:#fff;border:1px solid #dee2e6;border-radius:.25rem;box-shadow:0 .5rem 1rem rgba(0,0,0,.15)}#layout-error .logo{margin-bottom:1.5rem}#layout-error .page-content{font-size:.875rem;color:#495057;text-align:center}#layout-error h1,#layout-error h2{font-weight:400;line-height:1.2;color:#212529}#layout-error h1{margin:0 0 1.25rem;font-size:calc(1rem + 1.5vw)}#layout-error h2{margin:0 0 1rem;font-size:calc(1rem + 1vw);color:#212529}@media(min-width: 1200px){#layout-error h1{font-size:2.125rem}#layout-error h2{font-size:1.75rem}}@media(min-width: 768px){#layout-error{width:576px;padding:3.125rem 1rem;margin:3.5rem auto 0}#layout-error .logo{margin-bottom:2rem}#layout-error .page-content{font-size:1rem}}